* {
    margin: 0;
    padding: 0;
  }
  
  html {
    box-sizing: border-box;
  }
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  body {
    font-family: "Hind Vadodara", -apple-system, BlinkMacSystemFont, Segoe UI,
      Helvetica Neue, Arial, sans-serif;
  }
  .container {
    display: flex;
    height: 100vh;
    width: 100vw;
    flex-wrap: wrap;
    overflow: hidden;
  }
  .main {
    height: calc(100% - 50px);
    display: flex;
    flex: 1;
  }
  .sidebar {
    height: 100%;
    width: 220px;
    box-sizing: border-box;
    box-shadow: 0 0 2rem 0 rgb(0 0 0 / 5%);
    overflow: hidden;
    transition: width 0.5s ease;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .container.nav-closed .sidebar,
  .container.nav-closed .header-logo {
    width: 0;
  }
  .sidebar ul {
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  .sidebar ul li {
    display: flex;
    align-items: center;
  }
  .sidebar ul li a {
    color: #000;
    text-decoration: none;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
    white-space: nowrap;
  }
  .sidebar ul li a.active,
  .sidebar ul li a:hover {
    background: #e8ecef;
  }
  .sidebar ul li span {
    margin-left: 16px;
    font-size: 16px;
    font-weight: 100;
  }
  .sidebar ul li i {
    font-size: 18px;
    color: #111;
    font-weight: normal;
  }
  .header {
    height: 50px;
    background: #303f9f;
    width: 100%;
    display: flex;
    align-items: center;
    flex-basis: 100%;
  }
  .sidebar ul li a.active i {
    color: #303f9e;
  }
  .site-logo {
    height: 32px;
    width: 32px;
    min-height: 32px;
    min-width: 32px;
    margin: 0 18px 0 15px;
  }
  .site-logo path {
    fill: #fff;
  }
  .site-title {
    color: #fff;
    font-size: 24px;
    letter-spacing: 1px;
    font-weight: 400;
  }
  .page-content {
    padding: 10px 20px;
    box-sizing: border-box;
    width: 100%;
    flex: 1;
  }
  .page-content h1 {
    font-size: 20px;
    font-weight: 400;
    color: #333;
  }
  .header-search {
    height: 100%;
    align-items: center;
    display: flex;
    padding: 0 20px;
    flex: 1;
  }
  .header-search .button-menu {
    width: 28px;
    height: 28px;
    margin-right: 15px;
    background: none;
    border: 0;
    cursor: pointer;
  }
  .header-logo {
    display: flex;
    align-items: center;
    width: 220px;
    overflow: hidden;
    transition: width 0.5s ease;
  }
  .header-search input[type="search"] {
    height: 100%;
    width: 300px;
    padding: 10px 20px;
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 100;
    background: none;
    border: none;
    color: #fff;
  }
  .header-search input[type="search"]:focus {
    outline: none;
  }
  .header-search input[type="search"]::placeholder {
    color: #ccc;
  }
  .header-search .button-menu:focus {
    outline: none;
    border: none;
  }
  .header-search .button-menu svg path {
    fill: #fff;
  }
  @media screen and (max-width: 991px) {
    .page-content {
      width: 100vw;
    }
  }
  @media screen and (max-width: 767px) {
    .header-logo {
      display: none;
    }
  }
  
  .articles {
    padding: 0.5rem 0.75rem;
  }
  .articles li {
    margin: 0.5rem 0;
  }
  
  .auth {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.25);
  }
  
  form {
    width: 30%;
    padding: 2rem 1.5rem;
    background-color: #4b66c9;
    border-radius: 10px;
    position: relative;
  }
  
  input,
  label,
  .auth-btn {
    display: block;
    margin: 0.25rem;
  }
  
  input {
    width: 100%;
    padding: 0.5rem 0.35rem;
    border-radius: 5px;
    outline: none;
    border: none;
    font-size: 1.2rem;
    margin-bottom: 1rem;
    color: rgba(0, 0, 0, 0.548);
  }
  
  label {
    font-family: inherit;
    color: #fff;
    font-size: 1.1rem;
    font-weight: 300;
  }
  
  .auth-btn,
  .show-people {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    color: rgba(0, 0, 0, 0.829);
  }
  
  .center {
    text-align: center;
  }
  
  .auth h3 {
    font-size: 1.5rem;
    color: #e8ecef;
    margin: 0.5rem 0 2rem;
    font-weight: 400;
  }
  
  .auth-cancel {
    position: absolute;
    right: 2rem;
    top: 1.5rem;
    z-index: 1;
    font-size: 1.5rem;
    color: rgba(0, 0, 0, 0.781);
    font-weight: bolder;
    cursor: pointer;
  }
  
  .show-people {
    display: inline-block;
    border: 0.5px solid blue;
    margin: 1rem;
  }
  
  .flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .people {
    margin: 2rem auto;
    width: 30%;
    border: 1px solid blue;
    border-radius: 5px;
    padding: 0.5rem;
  }
  
  .logout {
    margin: 0 2rem;
    padding: 0.5rem 0.75rem;
    border: none;
    background-color: rgb(128, 128, 228);
    border-radius: 3px;
    color: white;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
  }
  .password-hint {
    margin-top: 1rem;
    color: white;
    text-align: center;
    font-size: 0.8rem;
  }
  
  .user-info {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
  }
  .user-info p {
    margin-left: 0.5rem;
  }
  
  .user-avatar {
    width: 35px;
    height: 35px;
    border-radius: 50%;
  }
  
  .bg {
    background: #303f9f;
    padding: 0.75rem 0;
  }
  
  .card {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 30%;
    padding: 1.5rem;
    border-radius: 5px;
    margin: 10rem auto;
  }
  
  /* On mouse-over, add a deeper shadow */
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  }
  
  .card-info {
    text-align: center;
    margin: 1rem;
  }
  
  .card-info h4 {
    margin: 1rem;
    font-size: 1.5rem;
    color: grey;
    text-transform: uppercase;
  }
  
  .m5 {
    margin: 5rem;
    color: gray;
    font-size: 2rem;
  }
  
  